.set_body {
  zoom: 85%;
}

.settings_main_box {
  width: 630px;
  height: 800px;
  position: absolute;
  top: 1500px;
  bottom: 1400px;
  right: 0px;
  left: 0px;
  background-color: white;
  margin: auto;
  border: 3px solid #000000;
  border-radius: 10px;
}

.set_title {
  height: 40px;
  position: absolute;
  top: 75px;
  bottom: 0px;
  right: 0px;
  left: 0px;
  text-transform: uppercase;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  letter-spacing: 2.5px;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 40px;
}

.set_id_icon {
  top: 16px;
  bottom: 0px;
  left: 22px;
  width: 35px;
  height: 35px;
  position: absolute;
}

.set_id_icon_image {
