  left: 22px;
  width: 35px;
  height: 35px;
  position: absolute;
}

.set_reset_button1 {
  width: 100%;
  height: 100%;
  cursor: pointer;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  background-color: #28afeedc;
  border: 4px line #000000;
  position: relative;
  padding: 12px;
  float: left;
  text-align: left;
  border-radius: 10px;
  right: 0px;
  left: 0px;
}
.set_reset_button_container1 {
  width: 400px;
